</template>

<script>
  import axios from 'axios'

  export default {
    data () {
      return {
        drawer: false,
        apiError: false,
        apiErrorResponse: "",
        loadingData: true,
        loaded: false,
        imageDialog: {},
        aboutDialog: false,
        items: [
        ],
        right: true,
        rightDrawer: false,
        title: 'Starlight -Re LIVE- DB βananaisu',
        pictureToDisplay: "",
        filteredTable: [],
        showFilterBadge: false,
        hover: {
          element: true,
          flower: false,
          wind: false,
          snow: false,
          cloud: false,
          moon: false,
          cosmos: false,
          dream: false,
          school: true,
          seisho: false,
          rinmeikan: false,
          frontier: false,
          seekfelt: false
        },
        allFiltersOff: true,
        characterToFilter: null,
        searchFilter: "",
        nameslist: {},
        filters:
          {
          star2Filter: false,
          star3Filter: false,
          star4Filter: false,
          normalFilter: false,
          specialFilter: false,
          flowerFilter: false,
          windFilter: false,
          snowFilter: false,
          cloudFilter: false,
          moonFilter: false,
          cosmosFilter: false,
          dreamFilter: false,
          frontFilter: false,
          middleFilter: false,
          backFilter: false,
          seishoFilter: false,
          seekfeltFilter: false,
          frontierFilter: false,
          rinmeikanFilter: false,
          characterFilter: false
        },
        headers: [
          { text: 'Rarity', value: 'rarity', align: 'left', width:'1%', class: 'rarityHeader'},
          { text: 'Name', value: 'subname', align: 'left'},
          { text: 'School', value: 'school', align: 'left'},
          { text: 'Element', value: 'element', align: 'left'},
          { text: 'Type', value: 'atktype', align: 'left'},
          { text: 'Position', value: 'position', align: 'left'},
          { text: 'Overall', value: 'overall', align: 'left'},
          { text: 'HP', value: 'hp', align: 'left' },
          { text: 'ATK', value: 'atk', align: 'left' },
          { text: 'DEF', value: 'def', align: 'left' },
          { text: 'MDEF', value: 'mdef', align: 'left' },
          { text: 'SPD', value: 'spd', align: 'left'},
        ],
        characterNames: [
          "Aijou Karen",
          "Kagura Hikari",
          "Tsuyuzaki Mahiru",
          "Saijou Claudine",
          "Tendou Maya",
          "Hoshimi Junna",
          "Daiba Nana",
          "Isurugi Futaba",
          "Hanayagi Kaoruko",
          "Tomoe Tamao",
          "Otonashi Ichie",
          "Akikaze Rui",
          "Yumeoji Fumi",
          "Tanaka Yuyuko",
          "Ootsuki Aruru",
          "Kano Misora",
          "Nonomiya Lalafin",
          "Ebisu Tsukasa",
          "Kochou Shizuha",
          "Yukishiro Akira",
          "Ootori Michiru",
          "Liu Meifan",
          "Tsuruhime Yachiyo",
          "Yumeoji Shiori"
        ],
        charadata: []
      }
    },
    methods: {
      checkAllFiltersOff: function() {
        for (var i in this.filters) {
          if (this.filters[i]) {
            return false;
          }
        }
        if (this.searchFilter != "") {
          return false;
        }
        return true;
      },
      resetFilters: function() {
        for (var i in this.filters) {
          this.filters[i] = false;
        }
        this.searchFilter = "";
        this.characterToFilter = null;
        this.filteredTable = this.charadata;
        this.showFilterBadge = false;
      },
      filterAtktype: function() {
        var filteredArray = {};
        for (let i of this.charadata) {
          if (this.filters.normalFilter) {
            if (i.atktype == "normal") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.specialFilter) {
            if (i.atktype == "special") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterElement: function() {
        var filteredArray = {};
        for (let i of this.charadata) {
          if (this.filters.flowerFilter) {
              if (i.element == "flower") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.windFilter) {
              if (i.element == "wind") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.snowFilter) {
              if (i.element == "snow") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.cloudFilter) {
              if (i.element == "cloud") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.moonFilter) {
              if (i.element == "moon") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.cosmosFilter) {
              if (i.element == "cosmos") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.dreamFilter) {
              if (i.element == "dream") {
                filteredArray[i.ID]= i;
              }
          }
        }
        return filteredArray;
      },
      filterPosition: function() {
        var filteredArray = {};

        for (let i of this.charadata) {

          if (this.filters.frontFilter) {
            if (i.position == "front") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.middleFilter) {
            if (i.position == "middle") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.backFilter) {
            if (i.position == "back") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterRarity: function() {
        var filteredArray = {};

        for (let i of this.charadata) {

          if (this.filters.star2Filter) {
            if (i.rarity == "2") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.star3Filter) {
            if (i.rarity == "3") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.star4Filter) {
            if (i.rarity == "4") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterSchool: function() {
        var filteredArray = {};

        for (let i of this.charadata) {

          if (this.filters.seishoFilter) {
            if (i.school == "seisho") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.rinmeikanFilter) {
            if (i.school == "rinmeikan") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.frontierFilter) {
            if (i.school == "frontier") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.seekfeltFilter) {
            if (i.school == "seekfelt") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterByName: function() {
        var filteredArray = {};

        for (let i of this.charadata) {
          if (this.filters.characterFilter) {
            if (i.charaname == this.characterToFilter) {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filteredBySearch: function() {
        var temp;
        var result = {};

        if (this.searchFilter != "") {
          temp = this.charadata.filter(i => {
            return i.subname.toLowerCase().includes(this.searchFilter.toLowerCase())
          });
          for (let i of temp) {
                result[i.ID]= i;
          }

          temp = this.charadata.filter(i => {
            return i.charaname.toLowerCase().includes(this.searchFilter.toLowerCase())
          });
          for (let i of temp) {
                result[i.ID]= i;
          }

          return result;
        } else {
          for (let i of this.charadata) {
                result[i.ID]= i;
          }
          return result;
        }

      },
      handleSelect: function() {
        if (this.characterToFilter !== undefined) {
          this.filters.characterFilter = true;
        } else {
          this.filters.characterFilter = false;
        }
      },
      intersection: function(input) {

      var result = [];
      var lists = input;

      for (let i in lists[0]) {
        var existsInAll = true;
        for (var j = 1; j < lists.length; j++) {
          if (!(i in lists[j])) {
            existsInAll = false;
            break;
          }
        }
        if (existsInAll) {
          result.push(lists[0][i]);
        }
      }
      return result;
      },
      getChardata: function() {
        axios
        .get('https://92r3xgypt5.execute-api.us-east-2.amazonaws.com/dev/database')
        .then(response => {
          this.charadata = response.data.Items;
          this.filteredTable = this.charadata;
          this.loadingData = false;
          this.loaded = true;
        })
        .catch(error => {
          this.apiError = true;
          this.apiErrorResponse = error;
        })
      },
      filterCharadata: function() {

        var stuffToFilter = [];

        if (this.filters.star2Filter || this.filters.star3Filter || this.filters.star4Filter) {
          stuffToFilter.push(this.filterRarity());
        }

        if (this.filters.normalFilter || this.filters.specialFilter) {
          stuffToFilter.push(this.filterAtktype());
        }

        if (this.filters.flowerFilter || this.filters.windFilter || this.filters.snowFilter || this.filters.cloudFilter || this.filters.moonFilter || this.filters.cosmosFilter || this.filters.dreamFilter) {

          stuffToFilter.push(this.filterElement());
        }

        if (this.filters.frontFilter || this.filters.middleFilter || this.filters.backFilter) {
          stuffToFilter.push(this.filterPosition());
        }

        if (this.filters.seishoFilter || this.filters.seekfeltFilter || this.filters.frontierFilter || this.filters.rinmeikanFilter) {
          stuffToFilter.push(this.filterSchool());
        }


        if (this.filters.characterFilter) {

          stuffToFilter.push(this.filterByName());
        }

        if (this.searchFilter != "") {
          stuffToFilter.push(this.filteredBySearch());
        }

        this.filteredTable = this.intersection(stuffToFilter);

        if (this.checkAllFiltersOff()) {
          this.filteredTable = this.charadata;
          this.showFilterBadge = false;
        } else {
          this.showFilterBadge = true;
        }
      },
      showNav: function () {
        this.$store.commit('toggleNav')
      }
    },
    computed: {

    },
    created(){
      if (!this.loaded) {
        this.getChardata()
      }
    }
  }
</script>

